import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { canvasRootBehavior, canvasErrorBehavior } from 'sn-canvas-core-addon';
import {
	isEmpty as _isEmpty
} from 'lodash';
import styles from './_sn-canvas-root.scss';

const SN_CANVAS_ROUTER = 'sn-canvas-router';
const SN_CANVAS_MAIN = 'sn-canvas-main';
const SN_CANVAS_TOOLBAR = 'sn-canvas-toolbar';
const SN_CANVAS_HEADER = 'sn-canvas-header';
const SN_CANVAS_ERROR = 'sn-canvas-error';
const SN_CANVAS_LAYOUT = 'sn-canvas-layout';
const SN_CANVAS_TABSDATA = 'sn-canvas-tabsdata';

const { COMPONENT_DOM_READY } = actionTypes;

const _setUpErrorHandler = host => {
	const rootCompID = host.getAttribute('component-id');
	const targetEle = host.querySelector(SN_CANVAS_ERROR);
	if (targetEle) {
		targetEle.open = `@${rootCompID}/showError`;
		targetEle.errorConfig = `@${rootCompID}/errorConfig`;
	} else {
		console.warn(`blank-canvas: can not found ${SN_CANVAS_ERROR}`);
	}
};

const SnCanvasRoot = ({ properties: { modalContent, rootSelectedNavItem, rootContentUpdated, showError, errorConfig } }) => {
	return (
		<div id="sn-canvas-root">
			<sn-canvas-modal-hoist modal-content={modalContent}></sn-canvas-modal-hoist>
			<sn-canvas-router
				root-selected-nav-item={rootSelectedNavItem}
				root-content-updated={rootContentUpdated} />
			<div className="layout-container">
				<slot></slot>
			</div>
		</div>
	);
};

createCustomElement('sn-canvas-root', {
	type: snabbdom,
	view: SnCanvasRoot,
	styles,
	behaviors: [canvasRootBehavior, canvasErrorBehavior],
	actionHandlers: {
		[COMPONENT_DOM_READY]: ({ host }) => {
			console.debug('host', host);
			const snCanvasRouter = host.shadowRoot.querySelector(SN_CANVAS_ROUTER);
			if (!snCanvasRouter) {
				console.error('Error, sn-canvas-router not found.');
				return;
			}

			let snCanvasLayout = host.querySelector(SN_CANVAS_LAYOUT);
			let snCanvasMain = host.querySelector(SN_CANVAS_MAIN);
			let snCanvasHeader = host.querySelector(SN_CANVAS_HEADER);
			let snCanvasToolbar = host.querySelector(SN_CANVAS_TOOLBAR);
			let snCanvasTabsData = host.querySelector(SN_CANVAS_TABSDATA);

			const routerSelectedContent = `@${snCanvasRouter.getAttribute('component-id')}/selectedContent`;

			if (snCanvasHeader) {
				snCanvasHeader.currentRoute = routerSelectedContent;
			}

			if (snCanvasToolbar) {
				snCanvasToolbar = host.querySelector(SN_CANVAS_TOOLBAR);
				snCanvasToolbar.activeRoute = routerSelectedContent;
			}

			if (snCanvasMain) {
				snCanvasMain.selectedContent = routerSelectedContent;
				if (!snCanvasLayout)
					return;
				const _componentId = snCanvasLayout.getAttribute('component-id');
				if (snCanvasLayout.screenStatus)
					snCanvasMain.screenStatus = `@${_componentId}/screenStatus`;
				if (snCanvasLayout.activityLogs)
					snCanvasMain.activityLogs = `@${_componentId}/activityLogs`;
				if (snCanvasLayout.closedNavigationIds)
					snCanvasMain.closedNavigationIds = `@${_componentId}/closedNavigationIds`;
			}

			if (snCanvasLayout) {
				snCanvasLayout.selectedContent = routerSelectedContent;
			}

			if (snCanvasTabsData) {
				snCanvasTabsData.selectedContent = routerSelectedContent;
			}

			_setUpErrorHandler(host);
		}
	}
});
